import React, { FC, Fragment } from 'react';
import cn from 'classnames';

import { BottomSheet, BottomSheetProps } from '@alfalab/core-components-bottom-sheet';
import { Button } from '@alfalab/core-components-button';
import { useMedia } from '@alfalab/hooks';

import { TooltipDesktop, TooltipDesktopProps } from './desktop';
import { useControlled } from './utils';

import styles from './responsive.module.css';

type View = 'desktop' | 'mobile';

export type TooltipResponsiveProps = Omit<TooltipDesktopProps, 'onClose' | 'onOpen'> & {
    /**
     *    
     */
    defaultMatch?: View;

    /**
     *  
     */
    onOpen?: (event?: React.MouseEvent<HTMLElement>) => void;

    /**
     *  
     */
    onClose?: (event?: React.MouseEvent<HTMLElement>) => void;

    /**
     *    
     */
    actionButtonTitle?: string;

    /**
     *   
     * @deprecated( bottomSheetProps.hasCloser)
     */
    hasCloser?: boolean;

    /**
     *     BottomSheet
     */
    bottomSheetProps?: Partial<BottomSheetProps>;

    /**
     *  ,    desktop 
     * @default 1024
     */
    breakpoint?: number;
};

export const TooltipResponsive: FC<TooltipResponsiveProps> = ({
    defaultMatch = 'mobile',
    content,
    children,
    open,
    onOpen,
    onClose,
    actionButtonTitle = '',
    hasCloser,
    targetRef,
    targetClassName,
    bottomSheetProps,
    breakpoint = 1024,
    ...restProps
}) => {
    const [view] = useMedia<View>(
        [
            ['mobile', `(max-width: ${breakpoint - 1}px)`],
            ['desktop', `(min-width: ${breakpoint}px)`],
        ],
        defaultMatch,
    );

    const [openValue, setOpenValueIfUncontrolled] = useControlled(open, false);

    const handleOpen = (event?: React.MouseEvent<HTMLElement>) => {
        if (onOpen) {
            onOpen(event);
        } else {
            setOpenValueIfUncontrolled(true);
        }
    };

    const handleClose = (event?: React.MouseEvent<HTMLElement>) => {
        if (onClose) {
            onClose(event);
        } else {
            setOpenValueIfUncontrolled(false);
        }
    };

    const isMobile = view === 'mobile';

    return isMobile ? (
        <Fragment>
            <BottomSheet
                {...restProps}
                open={Boolean(openValue)}
                onClose={handleClose}
                hasCloser={hasCloser}
                actionButton={
                    <Button view='secondary' block={true} size='s' onClick={handleClose}>
                        {actionButtonTitle}
                    </Button>
                }
                {...bottomSheetProps}
            >
                {content}
            </BottomSheet>
            {/** TODO:     */}
            {/* eslint-disable-next-line jsx-a11y/click-events-have-key-events, jsx-a11y/no-static-element-interactions */}
            <div
                ref={targetRef as React.Ref<HTMLDivElement>}
                onClick={handleOpen}
                className={cn(styles.target, targetClassName)}
            >
                {children?.props.disabled && <div className={styles.overlap} />}
                {children}
            </div>
        </Fragment>
    ) : (
        <TooltipDesktop
            {...restProps}
            open={open}
            content={content}
            onOpen={handleOpen}
            onClose={handleClose}
            targetClassName={targetClassName}
            targetRef={targetRef}
        >
            {children}
        </TooltipDesktop>
    );
};
